import {Command} from './command';
import {fabric} from 'fabric';

export class VrcholCommand implements Command{
  canvas:any;
  kruh: fabric.Circle;
  staryPocet:number;

  constructor(c: any,n:number){
    this.canvas = c;
    this.kruh = new fabric.Circle(
      {radius: 30,
      fill: '#f55',
      top: Math.floor(Math.random() * 740),
      left: Math.floor(Math.random() * 1540)
    });
    this.staryPocet=n;
  }


  execute() {
    this.canvas.add(this.kruh);
    return this.staryPocet+1;
  }

  unexecute() {
    this.canvas.remove(this.kruh);
    return this.staryPocet;
  }
}
